import { gql } from "@apollo/client";

export const LOGIN = gql`
  mutation login($email: String!, $password: String!) {
    login(email: $email, password: $password) {
      token
      user {
        _id
      }
    }
  }
`;

export const ADD_ORDER = gql`
  mutation addOrder($products: [ID]!) {
    addOrder(products: $products) {
      purchaseDate
      products {
        _id
        name
        description
        price
        quantity
        category {
          name
        }
      }
    }
  }
`;

export const ADD_USER = gql`
  mutation addUser(
    $firstName: String!
    $lastName: String!
    $email: String!
    $password: String!
  ) {
    addUser(
      firstName: $firstName
      lastName: $lastName
      email: $email
      password: $password
    ) {
      token
      user {
        _id
      }
    }
  }
`;

export const ADD_PRODUCT = gql`
  mutation addProduct(
    $name: String
    $category: String
    $description: String
    $image: String
    $quantity: Int
    $price: Float
  ) {
    addProduct(
      name: $name
      category: $category
      description: $description
      image: $image
      quantity: $quantity
      price: $price
    ) {
      description
      image
      name
      price
      quantity
    }
  }
`;

export const DEL_PRODUCT = gql`
  mutation delProduct($id: ID!) {
    delProduct(_id: $id) {
      _id
      name
    }
  }
`;
